.nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 18px;
    align-items: center;
    padding: 5px 20px;
    background-color: #3a53d4;
  }
  .nav__search {
    display: flex;
    align-items: center;
  }
  .nav__search__input {
    width: 100%;
    border: none;
    background-color: #3a53d4;
    color: #fff;
    margin-left: 4px;
    font-size: 1em;
  }
  
  .nav__search__input:focus {
    outline: none;
  }
  .nav__icons{
      display: flex;
      align-items: center;
  }
  .nav__avatar{
      margin-left: 4px;
    vertical-align: middle;
    width: 50px;
    height: 50px;
    border-radius: 50%
  }