.sidebar {
    display: flex;
    flex-direction: column;
    margin-top: 18px;
    width: 25%;
    align-items: center;
    background-color: rgba(26, 29, 34, 255);
  }
  .sidebar__top {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    background-color: #2647c8;
  }
  .sidebar__left {
    color: #fff;
    display: flex;
    align-items: center;
  }
  .sidebar__logo {
    width: 30px;
  }
  .sidebar__bottom {
    width: 100%;
  }
  
  .sidebar__group {
    width: 100%;
    margin-top: 20px;
    display: flex;
    color: white;
    flex-direction: column;
  }
  .sidebar__option {
    display: flex;
    align-items: center;
    color: white;
    padding: 10px 20px 10px 0;
  }
  .sidebar__option > * {
    margin-left: 10%;
  }
  .sidebar__option:hover {
    background-color: #0c0c0c;
    color: rgba(25, 64, 181, 255);
    border-left: 1px solid #2647c8;
  }
  .sidebar__genre {
    display: flex;
    color: white;
    flex-direction: column;
    padding: 10px 20px 10px 0;
    margin-left: 10%;
  }
  .sidebar__genre-bold-text {
    font-weight: bold;
    color: #fff;
    font-size: 1em;
    margin-bottom: 10px;
  }
  
  .sidebar__downloads {
    display: flex;
    flex-direction: column;
    padding: 10px 20px 10px 0;
    margin-left: 10%;
  }
  .sidebar__downloads-circle {
    padding: 5px 10px 5px 10px;
    background-color: #0c0c0c;
    width: fit-content;
    align-items: center;
    border-radius: 50%;
    position: absolute;
    left: 45vh;
    border-right: #2647c8 1px solid;
    margin-top: 20px;
  }
  .sidebar__downloads-cancel {
    color: red;
  }
  .sidebar__download-img {
    border-radius: 5px;
  }
  
  .sidebar__downloads-text {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
  .sidebar__downloads-details {
    width: 80%;
    margin-left: 10%;
    margin-top: -50px;
  }
  .sidebar__downloads-progress {
    display: flex;
    width: 100%;
    color: black;
  }